.contacts {
  padding: 50px 60px 100px;
  margin: 0 auto;
  max-width: 1160px;

  @media (max-width: $screen-xmd) {
    padding: 50px 35px 100px;
  }

  &__inner {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-left: 3px;
    padding-bottom: 100px;
  }

  &__title {
    font-size: 46px;
    margin-bottom: 0;
    padding-bottom: 50px;
  }

  &__text {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-right: 30px;
    width: 45%;
  }

  &__slider {
    position: relative;
    width: 500px;
    border: 1px solid $main-color;

    & .slick-prev,
    & .slick-next {
      position: absolute;
      width: 45px;
      height: 45px;
      bottom: -60px;
      border-radius: 50%;
      font-size: 0;
      background-color: $main-color;
      transition: all 0s;
    }

    & .slick-prev:hover,
    & .slick-next:hover {
      background-color: $white;
      box-shadow: none;
    }

    & .slick-prev:before,
    & .slick-next:before {
      content: '';
      display: inline-block;
      width: 20px;
      height: 20px;
      background-image: url(../img/right-arrow.svg);
      background-repeat: no-repeat;
      background-size: 100%;
    }

    & .slick-prev:hover:before,
    & .slick-next:hover:before {
      background-image: url(../img/right-arrow-blue.svg);
    }

    & .slick-prev {
      left: 150px;
    }

    & .slick-prev:before {
      transform: rotate(180deg);
    }

    & .slick-next {
      right: 150px;
    }

    & .slick-next:before {
      background-position: 2px 0;
    }
  }

  &__slider-item {
    width: 500px;
    height: 400px;
    border: 1px solid $main-color;
    box-sizing: border-box;
  }
}
